<template>
  <div class="login">
    <div class="container">
      <div class="main">
        <Form :model="form" ref="form" :rules="loginValidate">
          <FormItem label="用户名" prop="userName">
            <Input v-model="form.userName" size="large" clearable @keyup.enter="login"></Input>
          </FormItem>
          <FormItem label="密码" prop="passWord">
            <Input type="password" v-model="form.passWord" size="large" clearable @keyup.enter="login"></Input>
          </FormItem>
        </Form>
        <div class="flexEnd">
          <span class="hint">忘记密码？</span>
        </div>
        <div class="spaceBetween topSpace_large">
          <Checkbox>记住</Checkbox>
          <Button @click="login" class="btnPrimary">
            登录
          </Button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default{
    name:"login",
    data(){
      return {
        form:{
          userName:'',
          passWord:""
        },
        loginValidate:{
          userName:[
            {required:true,message:"用户名不可为空",trigger:"blur"}
          ],
          passWord:[
            {required:true,pattern:/^\S{6,}$/,message:"密码格式错误",trigger:"blur"}
          ]
        }
      }
    },
    mounted() {
    },
    methods:{
      login(){
        this.$refs.form.validate(valid=>{
          if(valid){
            this._axios("post","login",this.form,data=>{
              if(data){
                sessionStorage.opername = this.form.userName
                sessionStorage.password = this.form.passWord
                sessionStorage.token = data
                this.$router.push("/home")
              }
              
            })
          }
        })
      }
    }
  }
</script>

<style lang="less">
  .login{
    // height: 100vh;
    background: url(../assets/img/bg.jpg) no-repeat;
    background-size: cover;
    position: absolute;
    top: 0;
    bottom:0;
    left: 0;
    right: 0;
    // left: 50%;
    // transform: translate(-50%,-50%);
    .container{
      width: 400px;
      height: 375px;
      margin:10% auto;
      .logo{
        height: 50px;
        text-align: left;
        // background-color: skyblue;
      }
      .main{
        background-color: white;
        border-radius: 4px;
        box-shadow: 0px 0px 10px gray;
        padding: 15px 40px;
        & .ivu-form-item{
          margin-bottom: 15px;
        }
      }
    }
    
  }
</style>



